import React, { useState } from "react";
import loadScan from "../Images/loadScan.png";
import { CircularProgress } from "@chakra-ui/react";

let prevVal;

function QrGenerator() {
  const [src, setsrc] = useState("");
  const [loading, setloading] = useState(false);
  const [showQrPanel, setShowQrPanel] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");

  // Form fields
  const [fullName, setFullName] = useState("");
  const [email, setEmail] = useState("");
  const [contact, setContact] = useState("");
  const [profession, setProfession] = useState("");
  const [organization, setOrganization] = useState("");
  const [designation, setDesignation] = useState("");
  const [country, setCountry] = useState("");

  const genQr = async () => {
    setErrorMsg("");

    // Basic validation
    if (
      !fullName ||
      !email ||
      !contact ||
      !profession ||
      !organization ||
      !designation ||
      !country
    ) {
      setErrorMsg(" All fields are required.");
      return;
    }

    const dataObj = {
      fullName,
      email,
      contact,
      profession,
      organization,
      designation,
      country,
    };

    const combinedValue = Object.entries(dataObj)
      .map(([key, value]) => `${key}: ${value}`)
      .join("\n");

    if (prevVal === combinedValue) return;

    const button = document.getElementById("input");
    button.innerText = "Registering ...";

    try {
      // Send data to backend
      const response = await fetch("https://qrappserver-khaki.vercel.app/api/register", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(dataObj),
      });

      const result = await response.json();

      if (!response.ok) {
        setErrorMsg(result.error || "Something went wrong.");
        button.innerText = "Register";
        return;
      }

      // On success
      prevVal = combinedValue;
      setShowQrPanel(true);
      setsrc(
        `https://api.qrserver.com/v1/create-qr-code/?size=200x200&data=${encodeURIComponent(
          combinedValue
        )}`
      );
      setloading(true);
    } catch (err) {
      console.error(err);
      setErrorMsg("Network error. Please try again.");
    }
  };

  const visible = () => {
    setloading(false);
    const input = document.getElementById("input");
    input.innerText = "Register";
  };

  return (
    <div className="flex justify-center pt-10 bg-[rgb(219,226,255)] items-start min-h-[100vh]">
      <div className="text-black flex flex-col md:flex-row space-y-6 md:space-y-0 md:space-x-6 z-10">
        {/* Left Side - Form */}
        <div className="bg-white w-[35rem] space-y-4 px-8 shadow-xl rounded-lg py-6">
          <div className="flex justify-center">
            <p className="font-extrabold text-2xl text-[rgb(27,41,75)] w-full text-center">
              QR Code Registration
            </p>
          </div>

          {/* Error Message */}
          {errorMsg && (
            <div className="bg-red-100 text-red-700 px-4 py-2 rounded-md text-sm">
              {errorMsg}
            </div>
          )}

          {/* Form Fields */}
          <input
            type="text"
            value={fullName}
            onChange={(e) => setFullName(e.target.value)}
            placeholder="Full Name"
            className="w-full px-4 py-2 border-[2px] rounded-md outline-none text-lg"
          />
          <input
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            placeholder="Email"
            className="w-full px-4 py-2 border-[2px] rounded-md outline-none text-lg"
          />
          <input
            type="text"
            value={contact}
            onChange={(e) => setContact(e.target.value)}
            placeholder="Contact Number"
            className="w-full px-4 py-2 border-[2px] rounded-md outline-none text-lg"
          />
          <input
            type="text"
            value={profession}
            onChange={(e) => setProfession(e.target.value)}
            placeholder="Profession"
            className="w-full px-4 py-2 border-[2px] rounded-md outline-none text-lg"
          />
          <input
            type="text"
            value={organization}
            onChange={(e) => setOrganization(e.target.value)}
            placeholder="Organization"
            className="w-full px-4 py-2 border-[2px] rounded-md outline-none text-lg"
          />
          <input
            type="text"
            value={designation}
            onChange={(e) => setDesignation(e.target.value)}
            placeholder="Designation"
            className="w-full px-4 py-2 border-[2px] rounded-md outline-none text-lg"
          />
          <input
            type="text"
            value={country}
            onChange={(e) => setCountry(e.target.value)}
            placeholder="Country"
            className="w-full px-4 py-2 border-[2px] rounded-md outline-none text-lg"
          />

          {/* Register Button */}
          <div
            id="input"
            onClick={genQr}
            className="w-full text-white text-lg h-12 cursor-pointer font-bold flex justify-center items-center 
              rounded-md bg-[rgb(3,169,244)]"
          >
            Register
          </div>
        </div>

        {/* Right Side - QR Code Panel */}
        {showQrPanel && (
          <div className="space-y-4 px-4 relative rounded-lg w-80 py-6 shadow-xl bg-white">
            {/* Highlighted Message */}
            <div className="bg-yellow-100 border-l-4 border-yellow-500 text-yellow-900 p-4 rounded-md text-sm font-medium">
              <p>
                <strong> Please save this QR code</strong> as it serves as your
                official <strong>Entry Pass</strong>. Admission to the event will
                not be permitted without it.
              </p>
            </div>

            <div className="w-full h-80 flex justify-center items-center border-[1px] border-gray-500">
              {src && <img alt="" onLoad={visible} className="w-40" src={src} />}
              {!src && <img alt="" src={loadScan} className="w-40" />}
            </div>

            {loading && (
              <div className="absolute top-36 left-[7.5rem]">
                <CircularProgress isIndeterminate color="green.300" />
              </div>
            )}

            <div className="text-white font-bold space-x-3 h-12 flex cursor-pointer justify-center items-center rounded-md bg-[rgb(144,197,45)]">
              <i className="fa-sharp fa-solid text-2xl fa-circle-down"></i>
              <div className="text-center -space-y-1">
                <a target="_blank" rel="noreferrer" href={src} download="QR">
                  <p>Download</p>
                  <p className="text-xs text-[rgb(240,243,235)]">PNG</p>
                </a>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default QrGenerator;
