import React, { useState, useEffect } from 'react';
import { QrReader } from 'react-qr-reader';

const QrScanner = () => {
  const [rawScanned, setRawScanned] = useState('');
  const [parsedScanned, setParsedScanned] = useState(null);
  const [validEntries, setValidEntries] = useState([]);
  const [isVerified, setIsVerified] = useState(null);
  const [error, setError] = useState('');

  useEffect(() => {
    fetch('https://qrappserver-khaki.vercel.app/api/registrations')
      .then(res => {
        if (!res.ok) throw new Error(`HTTP error! status: ${res.status}`);
        return res.json();
      })
      .then(data => {
        if (Array.isArray(data)) setValidEntries(data);
        else setError('Invalid data format from backend');
      })
      .catch(err => {
        console.error(err);
        setError('Failed to load registration data.');
      });
  }, []);

  // Parse scanned string like:
  // fullName: Ashraf kerisi
  // email: ashrafkerisi@gmail.com
  // ...
  const parseScannedText = (text) => {
    const obj = {};
    const lines = text.split('\n');
    for (const line of lines) {
      const [key, ...rest] = line.split(':');
      if (!key || rest.length === 0) continue;
      obj[key.trim()] = rest.join(':').trim();
    }
    return obj;
  };

  useEffect(() => {
    if (!rawScanned) {
      setParsedScanned(null);
      setIsVerified(null);
      return;
    }

    const parsed = parseScannedText(rawScanned);
    setParsedScanned(parsed);

    const email = (parsed.email || '').toLowerCase().trim();

    const found = validEntries.some(entry => (entry.email || '').toLowerCase().trim() === email);

    setIsVerified(found);
    setError('');
  }, [rawScanned, validEntries]);

  const handleScan = (data) => {
    if (data && data !== rawScanned) {
      setRawScanned(data);
    }
  };

  const handleError = (err) => {
    console.error(err);
    setError('QR scanning error.');
  };

  return (
    <div style={styles.wrapper}>
      <h3>QR Scanner</h3>

      <div style={styles.reader}>
        <QrReader
          delay={300}
          onError={handleError}
          onResult={(result, error) => {
            if (result?.text) handleScan(result.text);
            if (error) console.warn(error);
          }}
          constraints={{ facingMode: 'environment' }}
          style={{ width: '100%' }}
        />
      </div>

      {rawScanned && (
        <div style={styles.resultBox}>
          {/* <p><strong>Raw Scanned Data:</strong></p>
          <pre style={stylescls.pre}>{rawScanned}</pre>

          {parsedScanned && (
            <>
              <p><strong>Parsed Object:</strong></p>
              <pre style={styles.pre}>{JSON.stringify(parsedScanned, null, 2)}</pre>
            </>
          )} */}

          <p style={{ color: isVerified ? 'green' : 'red', fontWeight: 'bold' }}>
            {isVerified === null ? '' : isVerified ? ' Record Found' : ' Record NOT Found'}
          </p>
        </div>
      )}

      {error && <p style={{ color: 'red' }}>{error}</p>}
    </div>
  );
};

const styles = {
  wrapper: {
    width: 320,
    margin: '0 auto',
    padding: 10,
    textAlign: 'center',
    fontFamily: 'sans-serif',
    border: '1px solid #ccc',
    borderRadius: 10,
    background: '#f9f9f9',
  },
  reader: {
    width: 300,
    margin: '0 auto',
  },
  resultBox: {
    marginTop: 10,
    textAlign: 'left',
    fontSize: 14,
  },
  pre: {
    backgroundColor: '#eee',
    padding: 10,
    borderRadius: 5,
    whiteSpace: 'pre-wrap',
    wordBreak: 'break-word',
  },
};

export default QrScanner;
